import { useMutation } from '@tanstack/react-query';
import { AxiosResponse } from 'axios';
import { Response, postToGooglesheet } from '../services/googlesheet';

const useGooglesheet = () => {
  const {
    data = [],
    isLoading,
    mutate,
  } = useMutation<AxiosResponse<Response>, unknown>({
    mutationFn: postToGooglesheet,
  });

  return {
    isLoading,
    data: data as any[],
    mutate,
  };
};

export default useGooglesheet;
