import { Form, Input, Select } from 'antd';
import { Img } from 'assets/img';
import PrimaryButton from 'components/Common/PrimaryButton';
import { useFormik } from 'formik';
import { useTranslation } from 'react-i18next';
import { regexEmailString, regexPhoneNumber } from './const';
import useGooglesheet from './hooks/useGooglesheet';

const { Option } = Select;

const initialValues = {
  goods: '',
  email: '',
  position: '',
  companyName: '',
  contactName: '',
  phoneNumber: '',
};

function RegisterForm() {
  const { t } = useTranslation();
  const { mutate } = useGooglesheet();

  const formik = useFormik({
    initialValues,
    onSubmit: values => {
      mutate(values as any);
    },
  });

  return (
    <div className="register">
      <div className="container">
        <h3 className="register__title">
          {t('register.Đăng ký trở thành đối tác của Fundiin')}
        </h3>
        <p className="register__subtitle">
          {t(
            'register.Để lại thông tin và tìm hiểu cơ hội hợp tác cùng chúng tôi ngay hôm nay!'
          )}
        </p>
      </div>
      <div className="container">
        <div className="register__form">
          <Form
            labelCol={{ span: 24 }}
            wrapperCol={{ span: 24 }}
            onFinish={formik.handleSubmit}
          >
            <Form.Item
              name="companyName"
              label={t('register.Tên doanh nghiệp')}
              rules={[{ required: true, message: t('required') }]}
            >
              <Input
                size="large"
                placeholder={t('register.Nhập tên doanh nghiệp')}
                onChange={formik.handleChange}
              />
            </Form.Item>
            <Form.Item
              name="goods"
              label={t('register.Ngành hàng')}
              rules={[{ required: true, message: t('required') }]}
            >
              <Select
                size="large"
                placeholder={t('register.Chọn ngành hàng')}
                onChange={value => formik.setFieldValue('goods', value)}
                allowClear
              >
                <Option value="Ngành hàng 1">Ngành hàng 1</Option>
                <Option value="Ngành hàng 2">Ngành hàng 1</Option>
                <Option value="Ngành hàng 3">Ngành hàng 1</Option>
              </Select>
            </Form.Item>
            <Form.Item
              name="phoneNumber"
              label={t('register.Số điện thoại liên hệ')}
              rules={[
                { required: true, message: t('required') },
                {
                  pattern: regexPhoneNumber,
                  message: t('Sai định dạng'),
                },
              ]}
            >
              <Input
                size="large"
                placeholder={t('register.Nhập số điện thoại')}
                onChange={formik.handleChange}
              />
            </Form.Item>
            <Form.Item
              name="contactName"
              label={t('register.Tên người liên hệ')}
              rules={[{ required: true, message: t('required') }]}
            >
              <Input
                size="large"
                placeholder={t('register.Nhập họ và tên')}
                onChange={formik.handleChange}
              />
            </Form.Item>
            <Form.Item
              name="email"
              label={t('register.Email')}
              rules={[
                { required: true, message: t('required') },
                {
                  pattern: regexEmailString,
                  message: t('Sai định dạng'),
                },
              ]}
            >
              <Input
                size="large"
                placeholder={t('register.Nhập email')}
                onChange={formik.handleChange}
              />
            </Form.Item>
            <Form.Item
              name="position"
              label={t('register.Chức danh')}
              rules={[{ required: true, message: t('required') }]}
            >
              <Input
                size="large"
                placeholder={t('register.Nhập chức danh')}
                onChange={formik.handleChange}
              />
            </Form.Item>

            <PrimaryButton type="submit" className="register-btn">
              {t('Gửi')}
            </PrimaryButton>
          </Form>
        </div>
      </div>
      <div className="register__bg">
        <img src={Img.registerBgUrl} alt="register bg" />
      </div>
    </div>
  );
}

export default RegisterForm;
